.btn {
  transition: background-color 0.5s ease;
}
.btn span {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  transition: color 0.5s ease;
}

.btn-def {
  border-radius: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 48px;
  background-color: #244c98;
  padding: 12px 12px 12px 24px;
  border: 1px solid #244c98;
}
.btn-def__icon {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-radius: 50%;
  transition: background-color 0.5s ease;
}
.btn-def__icon svg {
  width: 24px;
  height: 24px;
}
.btn-def__icon svg path {
  transition: stroke 0.5s ease;
}
.btn-def:hover {
  background-color: #fff;
}
.btn-def:hover span {
  color: #244c98;
}
.btn-def:hover .btn-def__icon {
  background-color: #244c98;
}
.btn-def:hover .btn-def__icon svg path {
  stroke: #fff;
}

@media (max-width: 1400px) {
  .btn span {
    font-size: 15px;
    line-height: 24px;
  }
  .btn-def {
    column-gap: 20px;
    padding: 10px 10px 10px 18px;
  }
  .btn-def__icon {
    width: 36px;
    height: 36px;
  }
}